import React from "react"
import * as scss from "./Person.module.scss"


const Person = ({name, url, affiliation, img}) => {

    return (
      <a href={url} target="_blank" rel="noreferrer" className={scss.link}>
    <div>
                <img src={img} alt={name} title={name} className={scss.roundImg} />
                <div className={scss.textReset}>
                    <div className={scss.name}>{name}</div>
                    <div className={scss.affiliation}>{affiliation}</div>
                </div>
    </div>
        </a>
  )
}

export default Person



